body {
  margin: 0;
  padding: 0;
  background-image: url("https://images.pexels.com/photos/1625426/pexels-photo-1625426.jpeg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  font-family: Arial, sans-serif;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.App h1 {
  color: rgba(0, 0, 0, 0.5);
  font-size: 3rem;
}

.App-header {
  text-align: center;
  padding: 2rem;
}

.weather {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1rem;
}

.weather input {
  width: 100%;
  padding: 0.5rem;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  margin-right: 0.5rem;
  background-color: rgba(0, 0, 0, 0.25);
}

.weather input[type="text"]::placeholder {
  color: #000;
}


.weather button {
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 4px;
  font-size: 1rem;
  background-color: rgba(0, 0, 0, 0.25);
  color: #000;
  cursor: pointer;
}

.results {
  border: 1px solid #000;
  border-radius: 15px;
  background-color: rgba(0, 0, 0, 0.25);
  padding: 2rem;
  margin: 1rem;
}

.results > div {
  margin-bottom: 0.5rem;
}

.results img {
  margin-bottom: 1rem;
}

@media (max-width: 768px) {
  .weather {
    flex-direction: column;
    align-items: center;
  }

  .weather input {
    margin-bottom: 0.5rem;
    width: auto;
  }

  .results {
    margin: 0.5rem;
  }
}

/* Media queries for tablet devices */

@media (min-width: 601px) and (max-width: 1024px) {
  .App h1 {
    color: rgba(0, 0, 0, 0.5);
    font-size: 2.5rem;
  }

  .weather input {
    font-size: 1rem;
  }

  .weather button {
    font-size: 1rem;
  }

  .results {
    font-size: 1rem;
  }
}

/* Media queries for mobile devices */
@media (max-width: 600px) {
  .App h1 {
    color: rgba(0, 0, 0, 0.5);
    font-size: 2rem;
  }

  .weather input {
    font-size: 0.8rem;
  }

  .weather button {
    font-size: 0.8rem;
  }

  .results {
    font-size: 0.8rem;
  }
}







